<template>
<div class="indexBox">

    <div class="indexLeft">
        <div class="personInfor">
            <img src="../../assets/imgs/headImg.jpg" class="headImg" >
            <div class="txtWrap">
                <p><span class="iconfont" >&#xe630;</span> 987136196</p>
                <p><span class="iconfont" >&#xe959;</span> 15602266688</p>
                <p><span class="iconfont" >&#xe63d;</span> caihaihong6083@163.com</p>
                <div class="chatTool">
                <a href="https://github.com/caihaihong"><span class="iconfont" >&#xe7ab;</span> </a>
                <span class="iconfont wechat" >&#xe66e;</span>  
                </div>
                <!-- <p><span class="iconfont" >&#xe6f3;</span> 123456</p> -->
            </div>
        </div>
        <img class="bottomQoute" src="../../assets/imgs/qoute.png">
    </div>
        <div class="indexRight">
            <div class="topBar"> 
                <ul class="topBarUl">
                    <li :class="{selected:topNavCurrent==i, topNavOption:true} " v-for="(topNavItem,i) in topNav" :key="i" @click="switchTab(i)">{{topNavItem.name}}</li>
                </ul>
            </div>
            <div class="leftContent">
                <div :class="{selectedArticle:true, animated:true, fadeInRight:showDetails, fadeOutRight:!showDetails}" v-if="showDetails">
                    <div class="closeTap iconfont" @click="showDetails=false">&#xe728;</div>
                    <div v-html="currentDetails.route" v-highlight class="content markdown-body">
                    </div>
                </div>
                <ul :class="{contentWrtap:true, animated:true, fadeOutLeft:showDetails}"  id="contentWrtap" v-if="!showDetails">
                    <li v-for="(item,index) in currentArrEssay " ref="articleBrife" :key="index"
                     @click="showDetailsEvent(item)">
                        <p class="title" v-html="index+1+'.'+item.titleValue"></p>
                        <p class="details">{{item.brife}}</p>
                        <p class="readMore">详细内容<span>>></span></p>
                    </li>

                </ul>
                <div class="copyRight" v-if="!showDetails">Powered by GitHub Copyright <br>© 2018 - 2019 Haihong Cai All Rights Reserved. </div>
                </div>

                <div class="rightNav">
                    <nav class="navList">
                        <ul class="navListContent">
                            <div class="leftLine"></div>
                            <li class="smallTitle" v-for="(item ,index) in topNavDetailsArr" :key="index" @click="chooseArea(index)">
                                <span :class="{cicle:true, cicleSelect:currentArea!=index}">
                                    </span><span class="num">{{index+1}}</span>
                                <p class="smallTitleTxt smallTitleTxtActive">{{item}}</p>
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
        </div>
</template>

<script>
import essay from './article.js';
import showdown from "showdown";
import readme from "../../../static/markdown/1.md";

export default {
    data() {
        return {
            topNav: essay.essayContent.title,
            topNavDetailsArr: [],
            topNavCurrent: 0,
            currentArrEssay: [],
            showDetails: false,
            currentDetails: '',
            articleLength: {
                length: 0,
                Arr: []
            },
            isScroll: false,
            currentArea: 0
        };
    },
    computed: {

    },
    components: {
        showdown
    },
    mounted() {
        this.init(0);

    },
    methods: {
        switchTab(index) {
            this.topNavCurrent = index;
            this.init(index)
        },
        showDetailsEvent(item) {
            this.showDetails = true;
            this.currentDetails = item;

        },
        init(index) {
            let that = this;
            that.currentArrEssay = [];
            that.articleLength.Arr = [];
            that.articleLength.Arr.push(0);
            that.articleLength.length = 0;
            let converter = new showdown.Converter();
            this.topNavDetailsArr = essay.essayContent.title[index].detailsArr;
            essay.essayContent.details[index].article.forEach((value, index) => {
                // 记录每一个区段文章的个数
                that.articleLength.length = that.articleLength.length + value.content.length;
                that.articleLength.Arr.push(value.content.length);
                value.content.forEach((subValue, index) => {
                    let essay = converter.makeHtml(subValue.route.toString());
                    let essayMatch = essay.match('</(\S*?)[^>]*>.*?');
                    value.content[index].titleValue = essay.slice(0, essayMatch.index) + essayMatch[0];
                    let blankEssay = essay.replace(/(<(\S*?)[^>]*>.*?)|\<.*?\>/g, '');
                    let titleLength = essay.slice(0, essayMatch.index).replace(/(<(\S*?)[^>]*>.*?)|\<.*?\>/g, '').length
                    value.content[index].brife = blankEssay.slice(7, 150) + '...';
                })
                that.currentArrEssay = that.currentArrEssay.concat(value.content);
            });
            //这样可以，nextTick里面的代码会在DOM更新后执行
            this.$nextTick(function () {
                document.getElementById('contentWrtap').scrollTop = 0
                document.getElementById('contentWrtap').addEventListener('scroll', that.handleScroll);
                //滚动容器的高度
                let containerHeight = document.getElementById('contentWrtap').clientHeight;
                //每一个元素的高度
                // console.log(that.articleLength.length)
                let EleHeight = (document.getElementById('contentWrtap').scrollHeight) / that.articleLength.length;
                // console.log(EleHeight, '每一个元素的高度');
                //每一类所在区间的范围
                that.articleLength.Arr.forEach((value, index) => {
                    if (index == 0) {
                        that.articleLength.Arr[index] = value * EleHeight;
                    } else {
                        that.articleLength.Arr[index] = (value * EleHeight) + that.articleLength.Arr[index - 1];
                    }
                })
            })
        },
        // 获取滚动条高度
        handleScroll() {
            if (this.isScroll) {
                //正在滚动
                return;
            }
            this.isScroll = true;
            //当前滚动条高度
            let contentWrtapscrollerTop = document.getElementById('contentWrtap').scrollTop;
            var nowIndex = this.binarySearch(this.articleLength.Arr, contentWrtapscrollerTop, 0, this.articleLength.Arr.length - 1);
            this.currentArea = nowIndex;
            if (nowIndex < 0) {
                this.currentArea = 0;
            }
            if (nowIndex > this.articleLength.Arr.length - 2) {
                this.currentArea = this.articleLength.Arr.length - 2;
            }
            // console.log(this.currentArea)
            this.isScroll = false;
            // console.log(document.getElementById('contentWrtap').scrollTop, 'scrollTop')
            // console.log(document.getElementById('contentWrtap').clientHeight, 'clientHeight')
            // console.log(document.getElementById('contentWrtap').scrollHeight, 'scrollHeight')
        },
        //区间检测
        binarySearch(arr, findVal, leftIndex, rightIndex) {
            console.log('数组', arr, findVal, leftIndex, rightIndex)
            console.log('滚动条', findVal)
            console.log('坐标', leftIndex, rightIndex)
            if (leftIndex > rightIndex) {
                var find = leftIndex;
                return find;
            }
            var midIndex = Math.floor((leftIndex + rightIndex) / 2);
            var midVal = arr[midIndex];
            if (midVal > findVal) {
                return this.binarySearch(arr, findVal, leftIndex, midIndex - 1);
            } else if (midVal < findVal) {
                return this.binarySearch(arr, findVal, midIndex + 1, rightIndex);
            } else {
                var find = midIndex;
                return find;
            }
        },
        chooseArea(index) {
            let that = this;
            let contentWrtapscrollerTop = document.getElementById('contentWrtap').scrollTop;
            //当下标为0时候，滚到最顶
            // if (index == 0) {
            //     document.getElementById('contentWrtap').scrollTop = 0;
            // } else {
            console.log(document.getElementById('contentWrtap').scrollTop);
            console.log(that.articleLength.Arr[index]);
            $('#contentWrtap').animate({
                scrollTop: that.articleLength.Arr[index]
            }, 500);
            // }

        }

    },
};
</script>

<style lang="scss" scoped>
@import "index.scss";
</style>



